




import { Component, Prop, Vue } from 'vue-property-decorator'
import myMixins from '../mixins/resize'
declare let echarts: any;

@Component({
  mixins:[myMixins]
})
export default class LineChart extends Vue{
  chart: any
  __resizeHandler: any

  public drawChart(): void {
    this.chart = echarts.init(document.getElementById('pieChart'));
    this.chart.setOption({
      title : {
        text: '',
        subtext: '',
        x:'center'
      },
      tooltip : {
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },
      legend: {
        orient: 'vertical',
        left: 'left',
        data: ['','','','','']
      },
      series : [
        {
          name: '',
          type: 'pie',
          radius : '55%',
          center: ['50%', '60%'],
          data:[
            {value:335, name:''},
            {value:310, name:''},
            {value:234, name:''},
            {value:135, name:''},
            {value:1548, name:''}
          ],
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      ]
    })
  }

  private mounted() {
    this.drawChart()
  }
}
